(function() {
  'use strict';
  angular.module('civicClient', [
    // vendor modules
    'ui.router',
    'ct.ui.router.extras',
    'angular-loading-bar',
    'mentio',
    'ngTouch',
    'ngDialog',
    'ngMessages',
    'duScroll',
    'ui.bootstrap',
    'ui.grid',
    'ui.grid.autoResize',
    'ui.grid.pagination',
    'ui.grid.cellNav',
    'ui.grid.exporter',
    'formly',
    'dialogs.main',
    'yaru22.angular-timeago',
    'angulartics',
    'angulartics.google.analytics',
    'monospaced.elastic',

    // config
    'civic.states',

    // app services
    'civic.config',
    'civic.services',
    'civic.security',
    'civic.login',
    'civic.common',
    'angular-lodash',

    // app root views
    'civic.pages',
    'civic.activity',
    'civic.search',
    'civic.account',
    'civic.browse',
    'civic.events',
    'civic.sources',
    'civic.users',
    'civic.add',
    'civic.community',
    'civic.curation'
  ])
    .run(appRun)
    .config(appConfig);

// @ngInject
  function appConfig($uiViewScrollProvider, $anchorScrollProvider, formlyConfigProvider, $compileProvider) {
    window.apiCheck.disabled = false; // set to true in production
    $compileProvider.debugInfoEnabled(true); // set to false in production

    formlyConfigProvider.extras.removeChromeAutoComplete = true;
    $uiViewScrollProvider.useAnchorScroll();
    $anchorScrollProvider.disableAutoScrolling();
  }

// @ngInject
  function appRun(Security, $rootScope, $http, $state, $analytics, $window, $location, _) {
    $window.loading_screen.finish();
    $rootScope.view = {};

    // ensure $state is globally addressable/injectable
    $rootScope.$state = $state;

    Security.requestCurrentUser();

    // client header identifier
    $http.defaults.headers.common['Civic-Web-Client-Version'] = '0.0.6';

    $rootScope.$on('$stateChangeSuccess', function (event, toState) {
      $rootScope.view.navMode = toState.data.navMode;
      if(_.isEmpty($location.hash())) { $rootScope.prevScroll = null; }
      $analytics.eventTrack(toState.name);
      $analytics.pageTrack(window.location.hash);
    });
    $rootScope.$on('duScrollspy:becameActive', function($event, $element, $target){
      //Automatically update location
      var hash = $element.prop('hash');
      if (hash) {
        history.replaceState(null, null, hash);
      }
    });
    /*  ui-router debug logging - uncomment these if you run into links/routes silently failing. */
    // function message(to, toP, from, fromP) {
    //   return from.name + angular.toJson(fromP) + ' -> ' + to.name + angular.toJson(toP);
    // }
    //
    // $rootScope.$on('$stateChangeStart', function (evt, to, toP, from, fromP) {
    //   console.log('Start:   ' + message(to, toP, from, fromP));
    // });
    // $rootScope.$on('$stateChangeSuccess', function (evt, to, toP, from, fromP) {
    //   console.log('Success: ' + message(to, toP, from, fromP));
    // });
    // $rootScope.$on('$stateChangeError', function (evt, to, toP, from, fromP, err) {
    //   console.error('Error:   ' + message(to, toP, from, fromP), err);
    // });

  }

// define root modules & dependencies
  angular.module('civic.security', [
    'civic.security.authorization',
    'civic.security.service',
    'civic.security.interceptor',
    'civic.security.login'
  ]);
  angular.module('civic.config', ['formly', 'formlyBootstrap']);
  angular.module('civic.states', ['ui.router']);
  angular.module('civic.sources', []);
  angular.module('civic.services', ['ui.router', 'ngResource', 'angular-lodash/filters']);
  angular.module('civic.pages', ['civic.security.authorization', 'ui.router']);
  angular.module('civic.account', ['civic.security.authorization', 'ui.router']);
  angular.module('civic.common', ['ui.router']);
  angular.module('civic.login', ['ui.router']);
  angular.module('civic.browse', ['ui.grid.selection', 'ui.grid.pagination', 'ui.router']);
  angular.module('civic.search', ['ui.router']);
  angular.module('civic.activity', ['ui.router']);
  angular.module('civic.community', ['ui.router']);
  angular.module('civic.curation', ['ui.router']);

  angular.module('civic.users', [
    'ui.router',
    'civic.users.profile',
    'civic.users.common'
  ]);

  angular.module('civic.add', [
    'ui.router',
    'formly',
    'formlyBootstrap',
    'civic.add.evidence'
  ]);

  angular.module('civic.events', [
    'ui.router',
    'ui.grid',
    'ngDialog',
    'formly',
    'formlyBootstrap',
    'angular-lodash/filters',
    'civic.events.common',
    'civic.events.genes',
    'civic.events.variants',
    'civic.events.variantGroups',
    'civic.events.evidence'
  ]);

})();

(function(){
  'use strict';
  angular.module('civic.states')
    .config(routesConfig);

  // @ngInject
  function routesConfig($stateProvider, $urlRouterProvider) {

    // 404
    $urlRouterProvider.otherwise('home');

    // abstract state redirects
    $urlRouterProvider.when('/events/genes/:geneId', '/events/genes/:geneId/summary');
    $urlRouterProvider.when('/events/genes', '/browse');
    $urlRouterProvider.when('/events', '/browse');

    // NOTE: data.titleExp in state configs are ng expressions parsed by the TitleService

    // static frontend pages
    $stateProvider
      .state('home', {
        url: '/home',
        controller: 'HomeCtrl',
        templateUrl: 'app/pages/home.tpl.html',
        data: {
          titleExp: '"Home"',
          navMode: 'home'
        }
      })
      .state('about', {
        url: '/about',
        controller: 'AboutCtrl',
        templateUrl: 'app/pages/about.tpl.html',
        reloadOnSearch: false,
        data: {
          titleExp: '"About"',
          navMode: 'sub'
        }
      })
      .state('participate', {
        url: '/participate',
        controller: 'ParticipateCtrl',
        templateUrl: 'app/pages/participate.tpl.html',
        data: {
          titleExp: '"Participate"',
          navMode: 'sub'
        }
      })
      .state('faq', {
        url: '/faq',
        templateUrl: 'app/pages/faq.tpl.html',
        data: {
          titleExp: '"FAQ"',
          navMode: 'sub'
        }
      })
      .state('meetings', {
        url: '/meetings',
        templateUrl: 'app/pages/meetings.tpl.html',
        data: {
          titleExp: '"Meetings"',
          navMode: 'sub'
        }
      })
      .state('glossary', {
        url: '/glossary',
        templateUrl: 'app/pages/glossary.tpl.html',
        controller: 'GlossaryCtrl',
        data: {
          titleExp: '"Glossary"',
          navMode: 'sub'
        }
      })
      .state('releases', {
        url: '/releases',
        templateUrl: 'app/pages/releases.tpl.html',
        controller: 'ReleasesController',
        controllerAs: 'vm',
        data: {
          titleExp: '"Data Releases"',
          navMode: 'sub'
        }
      })
      .state('api', {
        url: '/api-documentation',
        templateUrl: 'app/pages/apiDocumentation.tpl.html',
        data: {
          titleExp: '"API Documentation"',
          navMode: 'sub'
        }
      })
      .state('graphics', {
        url: '/graphics',
        templateUrl: 'app/pages/graphics.tpl.html',
        data: {
          titleExp: '"Presentation Graphics"',
          navMode: 'sub'
        }
      })
      .state('contact', {
        url: '/contact',
        controller: 'ContactCtrl',
        templateUrl: 'app/pages/contact.tpl.html',
        data: {
          titleExp: '"Contact"',
          navMode: 'sub'
        }
      });
  }
})();

(function() {
  'use strict';
  angular.module('civic.common')
    .filter('labelify', labelifyFilter)
    .filter('arrayToList', arrayToListFilter)
    .filter('encodeUri', encodeUri)
    .filter('capitalize', capitalizeFilter)
    .filter('decodeUri', decodeUri)
    .filter('ifEmpty', ifEmpty)
    .filter('unsafe', unsafe)
    .filter('ordinal', ordinal)
    .filter('keyToLabel', keyToLabel)
    .filter('words', words);

  // @ngInject
  function labelifyFilter() {
    return function (input) {
      input = input.replace(/_/g, ' ');
      return input.replace(/(?:^|\s)\S/g, function(a) { return a.toUpperCase(); });
    };
  }

  // @ngInject
  function words() {
    return function (input, words) {
      if (isNaN(words)) {
        return input;
      }
      if (words <= 0) {
        return '';
      }
      if (input) {
        var inputWords = input.split(/\s+/);
        if (inputWords.length > words) {
          input = inputWords.slice(0, words).join(' ') + '\u2026';
        }
      }
      return input;
    };
  }

  // @ngInject
  function arrayToListFilter(_) {
    return function(input, limitTo, terminator, showTotal) {
      limitTo = parseInt(limitTo, 10);
      terminator = terminator ? terminator : '';
      if (_.isArray(input) && parseInt(limitTo, 10)) {
        var output = input.slice(0,limitTo).join(', ').concat(terminator);
        if(showTotal && input.length > limitTo) {
          output = output + '<span class="more"> (' + String(input.length - limitTo) + ' more)</span>';
        }
        return output;
      } else if (_.isArray(input)) {
        return input.join(', ');
      } else {
        return input;
      }
    };
  }

  // @ngInject
  function encodeUri($window) {
    return function (input) {
      return $window.encodeURIComponent(input);
    };
  }

  // @ngInject
  function decodeUri($window) {
    return function (input) {
      return $window.decodeURIComponent(input);
    };
  }

  // @ngInject
  function keyToLabel(_) {
    return function (input) {
      if (!_.isNull(input) && !_.isUndefined(input)) {
        input = input.replace(/_/g, ' ');
        return input.substring(0, 1).toUpperCase() + input.substring(1);
      } else {
        return;
      }
    };
  }

  // @ngInject
  function capitalizeFilter(_) {
    return function (input) {
      if (!_.isNull(input) && !_.isUndefined(input)) {
        input = input.toLowerCase();
        return input.substring(0, 1).toUpperCase() + input.substring(1);
      } else {
        return;
      }
    };
  }

  // @ngInject
  function ifEmpty() {
    return function(input, defaultValue) {
      if (angular.isUndefined(input) || input === null || input === '') {
        return defaultValue;
      }

      return input;
    };
  }

  // @ngInject
  function unsafe($sce) { return $sce.trustAsHtml; }

  // @ngInject
  function ordinal () {
    return function(input) {
      var num = parseInt(input, 10);
      return isNaN(num) ? input : num + suffix(num);
    };

    function suffix (n) {
      return Math.floor(n / 10) === 1
        ? 'th'
        : (n % 10 === 1
        ? 'st'
        : (n % 10 === 2
        ? 'nd'
        : (n % 10 === 3
        ? 'rd'
        : 'th')));
    }
  }

})();

(function() {
  'use strict';
  angular.module('civic.config')
    .constant('formConfig', {
      options: {
        labelColWidth: 2,
        inputColWidth: 5,
        helpColWidth: 5
      },
      validationMessages: {
        '400': 'Bad Request',
        '401': 'Unauthorized',
        '403': 'Forbidden',
        '404': 'Not Found',
        '405': 'Method Not Allowed',
        '406': 'Not Acceptable',
        '409': 'Conflict',
        '500': 'Server Error',
        '501': 'Not Implemented'
      },
      errorPrompts: {
        '400': 'The CIViC Client made a request the server didn\'t like, check the console output for more information.',
        '401': 'You must be logged in to perform that action.',
        '403': 'You do not have sufficient permissions to perform that action.',
        '404': 'The CIViC client made a request to a route that could not be found, check the console output for more information',
        '405': 'The CIViC client made a request that is not supported, check the console output for more information',
        '406': 'TOTALLY UNACCEPTABLE, I CAN\'T EVEN',
        '409': 'The attempted update conflicts with an existing record.',
        '500': 'The CIViC server experienced an unrecoverable error, please check the console output for more information.',
        '501': 'The CIViC client attempted to utilize an unimplemented route.'
      }
    })
    .run(formlyTemplatesRun);

  // @ngInject
  function formlyTemplatesRun(formlyValidationMessages) {
    // default messages
    formlyValidationMessages.addTemplateOptionValueMessage('minlength', 'minlength', '', 'is the minimum length', 'Too short.');
    formlyValidationMessages.addTemplateOptionValueMessage('maxlength', 'maxlength', '', 'is the maximum length', 'Too long.');
    formlyValidationMessages.addStringMessage('required', 'This field is required.');
    formlyValidationMessages.addStringMessage('notfound', 'Valid entity could not be found.');
  }
})();

(function() {
  'use strict';
  angular.module('civic.config')
    .config(formlyWrappersConfig);

  // @ngInject
  function formlyWrappersConfig(formlyConfigProvider, formConfig) {
    var inputColWidth = formConfig.options.inputColWidth;
    var labelColWidth = formConfig.options.labelColWidth;
    var helpColWidth = formConfig.options.helpColWidth;

    /*
     * ERROR MSG WRAPPERS
     */

    formlyConfigProvider.setWrapper({
      name: 'validationMessages',
      template: [
        '<formly-transclude></formly-transclude>',
        '<div class="validation"',
        'ng-if="options.validation.errorExistsAndShouldBeVisible"',
        'ng-messages="options.formControl.$error">',
        '<div class="field-message-error" ng-message="{{::name}}" ng-repeat="(name, message) in ::options.validation.messages">',
        '{{message(options.formControl.$viewValue, options.formControl.$modelValue, this)}}',
        '</div>',
        '</div>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'simpleHasError',
      template: '<div ng-class="{\'has-error\': showError}">' +
      '<formly-transclude></formly-transclude>' +
      '</div>'
    });

    formlyConfigProvider.setWrapper({
      name: 'mentioCommentMenus',
      templateUrl: 'components/forms/fieldWrappers/mentioCommentMenus.tpl.html'
    });

    /*
     * FIELD WRAPPERS
     */
    // TODO: get all template strings into their own files while preserving the ability to define col widths etc.
    formlyConfigProvider.setWrapper({
      // horizontal bootstrap row with label
      name: 'horizontalBootstrapLabel',
      template: [
        '<label for="{{::id}}" class="col-sm-'+ labelColWidth +' control-label">',
        '{{to.label}}<span ng-if="to.required" style="color:#999;">&nbsp;*</span>',
        '</label>',
        '<div class="col-sm-' + inputColWidth + '">',
        '<formly-transclude></formly-transclude>',
        '</div>'
      ].join(' ')
    });

    // horizontal bootstrap field with label and help column
    formlyConfigProvider.setWrapper({
      name: 'horizontalBootstrapHelp',
      template: [
        '<label for="{{::id}}" class="col-sm-'+ labelColWidth +' control-label">',
        '{{to.label}}<span ng-if="to.required" style="color:#999;">&nbsp;*</span>',
        '</label>',
        '<div class="col-sm-'+ inputColWidth +'">',
        '<formly-transclude></formly-transclude>',
        //'<p class="small">errors:</p>',
        //'<div ng-messages-include="commentValidation.tpl.html"></div>',
        '</div>',
        '<div class="col-sm-'+ helpColWidth +' control-help">',
        '<span class="small" ng-bind-html="to.helpText"></span>',
        '</div>'
      ].join(' ')
    });

    // horizontal bootstrap field without label and help column (good for checkboxes)
    formlyConfigProvider.setWrapper({
      name: 'horizontalBootstrapHelpNoLabel',
      template: [
        '<label for="{{::id}}" class="col-sm-'+ labelColWidth +' control-label">',
        '</label>',
        '<div class="col-sm-'+ inputColWidth +'">',
        '<formly-transclude></formly-transclude>',
        //'<p class="small">errors:</p>',
        //'<div ng-messages-include="commentValidation.tpl.html"></div>',
        '</div>',
        '<div class="col-sm-'+ helpColWidth +' control-help">',
        '<span class="small" ng-bind-html="to.helpText"></span>',
        '</div>'
      ].join(' ')
    });

    // horizontal bootstrap checkbox
    formlyConfigProvider.setWrapper({
      name: 'horizontalBootstrapCheckbox',
      template: [
        '<div class="col-sm-offset-'+ labelColWidth +' col-sm-8">',
        '<formly-transclude></formly-transclude>',
        '</div>'
      ].join(' ')
    });

    // horizontal bootstrap with current user (intended for comment forms)
    formlyConfigProvider.setWrapper({
      name: 'horizontalBootstrapComment',
      template: [
        //'<label for="{{::id}}" class="col-sm-1 control-label">',
        //'<user-image user="to.currentUser" height="32" width="32"></user-image>',
        //'</label>',
        '<div class="col-sm-12">',
        '<formly-transclude></formly-transclude>',
        '</div>'
      ].join(' ')
    });

    // displays loading animation inside input
    formlyConfigProvider.setWrapper({
      name: 'loader',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="glyphicon glyphicon-refresh loader" ng-show="to.loading"></span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'pubdisplay',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small">Citation: {{ to.data.description }}</span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'variantTypeNotice',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small" ng-if="to.data.notice">.</span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'genedisplay',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small">Gene: {{ to.data.name }}</span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'entrezIdDisplay',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small">Entrez ID: {{ to.data.entrez_id }}</span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'diseasedisplay',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small">Disease Ontology ID: {{ to.data.doid}}</span>'
      ].join(' ')
    });

    formlyConfigProvider.setWrapper({
      name: 'attributeDefinition',
      template: [
        '<formly-transclude></formly-transclude>',
        '<span class="small" ng-bind-html="to.data.attributeDefinition"></span>'
      ].join(' ')
    });
  }
})();

(function() {
  'use strict';
  angular.module('civic.config')
    .config(commentConfig);

  // @ngInject
  function commentConfig(formlyConfigProvider) {
    formlyConfigProvider.setType({
      name: 'comment',
      extends: 'textarea',
      wrapper: ['validationMessages', 'horizontalBootstrapComment', 'bootstrapHasError', 'mentioCommentMenus'],
      data: {
        users: [],
        entities: []
      },
      controller: /* @ngInject */ function($scope, $filter, CommentSuggestions) {
        $scope.searchUsers = function(term) {
          CommentSuggestions.getUserSuggestions(term).then(function(response) {
            $scope.users = response;
          });
        };

        $scope.getUser = function(user) {
          return '@' + user.display_name;
        };

        $scope.searchEntities = function(term) {
          CommentSuggestions.getEntitySuggestions(term).then(function(response) {
            $scope.entities = _.map(response, function(entity) {
              entity.display_type = $filter('keyToLabel')(entity.type).toUpperCase();
              return entity;
            });
          });
        };

        $scope.getEntity= function(entity) {
          var types = {
            gene: 'G',
            variant: 'V',
            variant_group: 'VG',
            evidence_item: 'E',
            revision: 'R'
          };
          return '#' + types[entity.type] + entity.id;
        };

        $scope.typedTerm = '';
      }
    });
  }

})();

(function() {
  'use strict';
  angular.module('civic.config')
    .config(basicFieldTypesConfig);

  // @ngInject
  function basicFieldTypesConfig(formlyConfigProvider) {
    /*
     * BASIC FIELD TYPES
     * Two versions of each basic field type, one with a help column and one without
     */
    // input
    formlyConfigProvider.setType({
      name: 'basicInput',
      template: '<input class="form-control" ng-model="model[options.key]">'
    });

    formlyConfigProvider.setType({
      name: 'horizontalInput',
      extends: 'input',
      wrapper: ['horizontalBootstrapLabel', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalInputHelp',
      extends: 'input',
      wrapper: ['horizontalBootstrapHelp', 'bootstrapHasError']
    });

    // select
    formlyConfigProvider.setType({
      name: 'horizontalSelect',
      extends: 'select',
      wrapper: ['horizontalBootstrapLabel', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalSelectHelp',
      extends: 'select',
      wrapper: ['horizontalBootstrapHelp', 'bootstrapHasError']
    });

    // textarea
    formlyConfigProvider.setType({
      name: 'horizontalTextarea',
      extends: 'textarea',
      wrapper: ['validationMessages', 'horizontalBootstrapLabel', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalTextareaHelp',
      extends: 'textarea',
      wrapper: ['horizontalBootstrapHelp', 'bootstrapHasError']
    });


    // checkbox
    formlyConfigProvider.setType({
      name: 'horizontalCheckbox',
      extends: 'checkbox',
      wrapper: ['horizontalBootstrapCheckbox', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalCheckboxHelp',
      extends: 'checkbox',
      wrapper: ['horizontalBootstrapHelpNoLabel', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalCommentHelp',
      extends: 'comment',
      wrapper: ['horizontalBootstrapHelp', 'bootstrapHasError']
    });

    /*
    * EXTENDED FIELD TYPES
    * (more complex behavior than the basics, but no controller, template, or additional logic)
    */

    formlyConfigProvider.setType({
      name: 'input-loader',
      extends: 'input',
      wrapper: ['loader']
    });
  }
})();

(function() {
  'use strict';
  angular.module('civic.config')
    .config(ratingConfig);

  // @ngInject
  function ratingConfig(formlyConfigProvider) {
    formlyConfigProvider.setType({
      name: 'rating',
      templateUrl: 'components/forms/fieldTypes/rating.tpl.html',
      controller: /* @ngInject */ function($scope) {
        $scope.overStar = $scope.model.rating;
        $scope.hoveringOver= function(value) {
          $scope.overStar = value;
        };

        $scope.leave = function() {
          $scope.model.rating === 0 ? $scope.overStar = 0 : $scope.overStar = $scope.model.rating;
        };
      }
    });

    formlyConfigProvider.setType({
      name: 'horizontalRating',
      extends: 'rating',
      wrapper: ['horizontalBootstrapLabel', 'bootstrapHasError']
    });

    formlyConfigProvider.setType({
      name: 'horizontalRatingHelp',
      extends: 'rating',
      wrapper: ['horizontalBootstrapHelp', 'bootstrapHasError']
    });

  }

})();

