(function() {
  'use strict';
  angular.module('civic.services')
    .factory('VariantsResource', VariantsResource)
    .factory('Variants', VariantsService);

  // @ngInject
  function VariantsResource($resource, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    var cacheInterceptor = function(response) {
      // console.log(['VariantsResource: removing', response.config.url, 'from $http cache.'].join(" "));
      cache.remove(response.config.url);
      return response.$promise;
    };

    return $resource('/api/variants/:variantId',
      {
        variantId: '@variantId'
      },
      {
        // Base Variant Resources
        query: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        get: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        update: {
          method: 'PATCH',
          interceptor: {
            response: cacheInterceptor
          }
        },
        delete: {
          method: 'DELETE',
          interceptor: {
            response: cacheInterceptor
          }
        },
        apply: {
          method: 'PATCH',
          cache: false
        },

        // Variant Collections
        queryEvidence: {
          method: 'GET',
          url: '/api/variants/:variantId/evidence_items',
          isArray: false,
          cache: cache
        },

        // Variant Comments Resources
        queryComments: {
          method: 'GET',
          url: '/api/variants/:variantId/comments',
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/variants/:variantId/comments/:commentId',
          params: {
            variantId: '@variantId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },
        submitComment: {
          method: 'POST',
          url: '/api/variants/:variantId/comments',
          params: {
            variantId: '@variantId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/variants/:variantId/comments/:commentId',
          params: {
            variantId: '@variantId',
            commentId: '@commentId'
          },
          cache: false
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/variants/:variantId/comments/:commentId',
          params: {
            variantId: '@variantId',
            commentId: '@commentId'
          },
          cache: false
        },
        queryVariantTypes: {
          method: 'GET',
          url: '/api/variant_types',
          params: {
            name: '@name',
            count: '@count',
            page: '@page'
          },
          cache: false
        },
        queryVariantTypeRelationships: {
          method: 'GET',
          url: '/api/variant_types/relationships',
          params: {
            new_variant_type_id: '@new_variant_type_id',
            existing_variant_type_ids: '@existing_variant_type_ids'
          },
          paramSerializer: '$httpParamSerializerJQLike',
          isArray: true
        }
      }
    );
  }

  // @ngInject
  function VariantsService(VariantsResource, $q, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    // Base Variant and Variant Collection
    var item = {};
    var collection = [];

    // Variant Collections
    var evidence = [];
    var comments = [];

    return {
      initBase: initBase,
      initComments: initComments,
      data: {
        item: item,
        collection: collection,
        evidence: evidence,
        comments: comments
      },

      // Variant Base
      query: query,
      get: get,
      update: update,
      delete: deleteItem,
      apply: apply,

      // Variant Collections
      queryEvidence: queryEvidence,

      // Variant Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment,

      queryVariantTypes: queryVariantTypes,
      queryVariantTypeRelationships: queryVariantTypeRelationships
    };

    function initBase(variantId) {
      return $q.all([
        get(variantId),
        queryEvidence(variantId)
      ]);
    }

    function initComments(variantId) {
      return $q.all([
        queryComments(variantId)
      ]);
    }
    // Variant Base
    function query() {
      return VariantsResource.query().$promise
        .then(function(response) {
          angular.copy(response.records, collection);
          return response.$promise;
        });
    }
    function get(variantId) {
      return VariantsResource.get({variantId: variantId}).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }
    function update(reqObj) {
      return VariantsResource.update(reqObj).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }
    function deleteItem(variantId) {
      return VariantsResource.delete({variantId: variantId}).$promise
        .then(function(response) {
          item = null;
          return response.$promise;
        });
    }
    function apply(reqObj) {
      return VariantsResource.apply(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/variants/' + response.id);
          get(reqObj.variantId);
          return $q.when(response);
        },
        function(error) { // fail
          return $q.reject(error);
        });
    }

    // Variant Collections
    function queryEvidence(variantId) {
      return VariantsResource.queryEvidence({variantId: variantId}).$promise
        .then(function(response) {
          angular.copy(response.records, evidence);
          return response.$promise;
        });
    }

    // Variant Comments
    function queryComments(variantId) {
      return VariantsResource.queryComments({variantId: variantId}).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(variantId, commentId) {
      return VariantsResource.getComment({variantId: variantId, commentId: commentId}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return VariantsResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + reqObj.variantId + '/comments');
          queryComments(reqObj.variantId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return VariantsResource.updateComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + reqObj.variantId + '/comments/' + reqObj.id);
          cache.remove('/api/variants/' + reqObj.variantId + '/comments');
          queryComments(reqObj.variantId);
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return VariantsResource.deleteComment({variantId: item.id, commentId: commentId}).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + item.id + '/comments');
          queryComments(item.id);
          return response.$promise;
        });
    }
    function queryVariantTypes(reqObj) {
      return VariantsResource.queryVariantTypes(reqObj).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function queryVariantTypeRelationships(reqObj) {
      //reqObj.existing_variant_type_ids = JSON.stringify(reqObj.existing_variant_type_ids);
      return VariantsResource.queryVariantTypeRelationships(reqObj).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
  }
})();

(function() {
  'use strict';
  angular.module('civic.services')
    .factory('VariantRevisionsResource', VariantRevisionsResource)
    .factory('VariantRevisions', VariantRevisionsService);

  function VariantRevisionsResource($resource, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    // adding this interceptor to a route will remove cached record
    var cacheResponseInterceptor = function(response) {
      cache.remove(response.config.url);
      return response.$promise;
    };

    return $resource('/api/variants/:variantId/suggested_changes/:revisionId',
      {
        variantId: '@variantId',
        revisionId: '@revisionId'
      },
      {
        // Base Variant Revisions Resources
        query: {
          method: 'GET',
          isArray: true,
          cache: cache
        },
        get: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        getPendingFields: {
          method: 'GET',
          url: '/api/variants/:variantId/fields_with_pending_changes',
          params: {
            variantId: '@variantId'
          },
          cache: false
        },
        submitRevision: {
          method: 'POST',
          cache: false
        },
        acceptRevision: {
          method: 'POST',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/accept',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId',
            force: true
          },
          cache: false
        },
        rejectRevision: {
          method: 'POST',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/reject',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId',
            force: true
          },
          cache: cache
        },

        // Variant Revisions Comments Resources
        submitComment: {
          method: 'POST',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/comments',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          interceptor: {
            response: cacheResponseInterceptor
          }
        },
        queryComments: {
          method: 'GET',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/comments',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId'
          },
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/variants/:variantId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantId: '@variantId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          cache: false
        }
      }
    );
  }

  function VariantRevisionsService(VariantRevisionsResource,
                                   Variants,
                                   Genes,
                                   $cacheFactory,
                                   $q) {
    // fetch variants cache, need to delete variant record when revision is submitted
    var cache = $cacheFactory.get('$http');

    // Base Variant Revision and Variant Revisions Collection
    var item = {};
    var parent = Variants.data.item;
    var collection = [];

    // Variant Revisions Comments
    var comment = {};
    var comments = [];

    var pendingFields = {};

    return {
      initBase: initBase,
      initRevisions: initRevisions,
      initComments: initComments,
      data: {
        item: item,
        parent: parent,
        collection: collection,
        comment: comment,
        comments: comments,
        pendingFields: pendingFields
      },

      // Variant Revisions Base
      query: query,
      get: get,
      getPendingFields: getPendingFields,
      submitRevision: submitRevision,
      acceptRevision: acceptRevision,
      rejectRevision: rejectRevision,

      // Variant Revisions Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment
    };

    function initBase(variantId, revisionId) {
      return $q.all([
        query(variantId, revisionId)
      ]);
    }

    function initRevisions(variantId) {
      return $q.all([
        query(variantId)
      ]);
    }

    function initComments(variantId, revisionId) {
      return $q.all([
        query(variantId, revisionId)
      ]);
    }

    // Variant Revisions Base
    function query(variantId) {
      return VariantRevisionsResource.query({ variantId: variantId }).$promise
        .then(function(response) {
          angular.copy(response, collection);
          return response.$promise;
        });
    }
    function get(variantId, revisionId) {
      return VariantRevisionsResource.get({ variantId: variantId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }

    function getPendingFields(variantId) {
      return VariantRevisionsResource.getPendingFields({ variantId: variantId }).$promise
        .then(function(response) {
          angular.copy(response.toJSON(), pendingFields);
          return response.$promise;
        });
    }

    function submitRevision(reqObj) {
      return VariantRevisionsResource.submitRevision(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/variants/' + reqObj.id + '/suggested_changes/');
          return $q.when(response);
        },
        function(error) { //fail
          return $q.reject(error);
        });
    }

    function acceptRevision(variantId, revisionId) {
      return VariantRevisionsResource.acceptRevision({
        variantId: variantId,
        revisionId: revisionId
      }).$promise.then(
        function(response) {
          // flush variant cache and refresh
          cache.remove('/api/variants/' + variantId );
          Variants.get(variantId);

          // flush variant suggested_changes and refresh
          cache.remove('/api/variants/' + variantId + '/suggested_changes/');
          query(variantId);

          // flush revision and refresh
          cache.remove('/api/variants/' + variantId + '/suggested_changes/' + revisionId);
          get(variantId, revisionId);

          // flush gene variants and refresh (for variant menu)
          cache.remove('/api/genes/' + response.gene_id + '/variants');
          Genes.queryVariants(response.gene_id);
          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }

    function rejectRevision(variantId, revisionId) {
      return VariantRevisionsResource.rejectRevision({ variantId: variantId, revisionId: revisionId }).$promise.then(
        function(response) {
          // flush variant suggested_changes and refresh
          cache.remove('/api/variants/' + variantId + '/suggested_changes/');
          query(variantId);

          // flush revision and refresh
          cache.remove('/api/variants/' + variantId + '/suggested_changes/' + revisionId);
          get(variantId, revisionId);

          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }

    // Variant Revisions Comments
    function queryComments(variantId, revisionId) {
      return VariantRevisionsResource.queryComments({ variantId: variantId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(variantId, revisionId, commentId) {
      return VariantRevisionsResource.getComment({ variantId: variantId, revisionId: revisionId, commentId: commentId }).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return VariantRevisionsResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + reqObj.variantId + '/suggested_changes/' + reqObj.revisionId + '/comments');
          queryComments(reqObj.variantId, reqObj.revisionId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return VariantRevisionsResource.updateComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + reqObj.variantId + '/suggested_changes/' + reqObj.revisionId + '/comments/' + reqObj.commentId );
          queryComments(reqObj.variantId, reqObj.revisionId);
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return VariantRevisionsResource.deleteComment({ variantId: parent.id, revisionId: item.id, commentId: commentId }).$promise
        .then(function(response) {
          cache.remove('/api/variants/' + parent.id + '/suggested_changes/' + item.id + '/comments');
          queryComments(parent.id, item.id);
          return response.$promise;
        });
    }
  }
})();

