(function() {
  'use strict';

  angular.module('civic.search')
    .directive('queryBuilder', queryBuilder)
    .controller('QueryBuilderController', QueryBuilderController);

  // @ngInject
  function queryBuilder() {
    var directive = {
      restrict: 'E',
      replace: true,
      scope: {
        entity: '=',
        fields: '='
      },
      controller: 'QueryBuilderController',
      templateUrl: 'app/views/search/directives/queryBuilder.tpl.html'
    };

    return directive;
  }

  // @ngInject
  function QueryBuilderController($scope, $state, $stateParams, $log, _, Search) {
    var vm = $scope.vm = {};
    var entity = vm.entity = $scope.entity;

    // function assignment
    vm.onSubmit = onSubmit;
    vm.options = {};
    vm.searchResults = [];
    vm.showGrid = true;

    vm.model = {
      operator: "",
      queries: {}
    };

    init();

    $scope.$watch('vm.model', function() {
      vm.formError = false;
    }, true);

    $scope.$on('setSearch', function(event, search) {
      if (!_.isUndefined(search)) {
        vm.model.operator = search.operator;
        vm.model.queries = search.queries;
        onSubmit();
      }
    });

    // function definition
    function onSubmit() {
      $log.debug(angular.toJson(vm.model));
      //vm.searchResults = Search.post(vm.model);
      //vm.showEvidenceGrid = true;
      vm.model.entity = entity;
      vm.model.save = true;
      vm.formError = false;
      Search.post(vm.model)
        .then(function(response) { // success
          vm.searchResults = response.results;
          vm.showGrid = true;
          if(_.has(response, 'token') && !_.isNull(response.token)) {
            var state = entity === 'evidence_items' ? 'search.evidence' : 'search.' + entity;
            $state.transitionTo(state, { token: response.token }, {notify: false});
          }
        },
        function(response) { // error
          angular.copy([], vm.searchResults);
          vm.showGrid = true;
          vm.formError = response;
        });
    }

    vm.buttonLabel = 'Search';

    function init() {
      if (_.has($stateParams, 'token') && !_.isEmpty($stateParams.token)) {
        Search.get({entity: entity, token: $stateParams.token})
          .then(function (response) {
            vm.model.operator = response.params.operator;
            vm.model.queries = response.params.queries;
            vm.searchResults = response.results;
            vm.showGrid = true;
          });
      } else {
        Search.reset();
        vm.model = {
          operator: 'AND',
          queries: [
            {
              field: '',
              condition: {
                name: undefined,
                parameters: []
              }
            }
          ]
        };
      }
    }
    vm.operatorField = [
      {
        key: 'operator',
        type: 'queryBuilderSelect',
        data: {
          defaultValue: 'AND'
        },
        templateOptions: {
          label: '',
          options: [
            { value: 'AND', name: 'all' },
            { value: 'OR', name: 'any' }
          ]
        }
      }
    ];

    vm.fields = $scope.fields;
  }
})();

(function() {
  'use strict';
  angular.module('civic.search')
    .config(queryBuilderConfig);

  // @ngInject
  function queryBuilderConfig(formlyConfigProvider) {
    var unique = 1;

    formlyConfigProvider.setType({
      name: 'queryBuilderSelect',
      extends: 'select',
      controller: /* @ngInject */ function($scope, $parse) {
        // angular-formly's options.defaultValue appears to ignore current model values,
        // so we must implement our own default value feature here
        var getter = $parse($scope.options.key);
        var setter = getter.assign;
        if(getter($scope.model) === undefined) {
          // no model value found, so set field value to default value
          setter($scope.model, $scope.options.data.defaultValue);
        }
      }
    });

    formlyConfigProvider.setType({
      name: 'queryRow',
      templateUrl: 'app/views/search/forms/queryBuilder.type.tpl.html',
      controller: /* @ngInject */ function($scope) {
        $scope.formOptions = {formState: $scope.formState};
        $scope.addNew = addNew;

        $scope.copyFields = copyFields;

        function copyFields(fields) {
          fields = angular.copy(fields);
          addRandomIds(fields);
          return fields;
        }

        function addNew() {
          $scope.model[$scope.options.key] = $scope.model[$scope.options.key] || [];
          var repeatsection = $scope.model[$scope.options.key];
          //var lastSection = repeatsection[repeatsection.length - 1];
          var newsection = { field: '', condition: { name: undefined, parameters: [] } };
          //if (lastSection) {
          //  newsection = angular.copy(lastSection);
          //}
          repeatsection.push(newsection);
        }

        function addRandomIds(fields) {
          unique++;
          angular.forEach(fields, function(field, index) {
            if (field.fieldGroup) {
              addRandomIds(field.fieldGroup);
              return; // fieldGroups don't need an ID
            }

            if (field.templateOptions && field.templateOptions.fields) {
              addRandomIds(field.templateOptions.fields);
            }

            field.id = field.id || (field.key + '_' + index + '_' + unique + getRandomInt(0, 9999));
          });
        }

        function getRandomInt(min, max) {
          return Math.floor(Math.random() * (max - min)) + min;
        }
      }
    });
  }

})();

