(function() {
  'use strict';
  angular.module('civic.sources')
    .controller('SourcesSummaryController', SourcesSummaryController);

  // @ngInject
  function SourcesSummaryController($scope, source, Sources, Security, Search) {
    console.log('SourcesSummaryController called.');
    var vm = $scope.vm = {};

    vm.isEditor = Security.isEditor();

    function parseSource (source) {
      source.author_list_string = _(source.author_list)
        .sortBy('position')
        .map(function(author) {
          return [author.fore_name, author.last_name].join(' ');
        })
        .value()
        .join(', ');

      // format publication date
      var pubDate = [source.publication_date.year];
      if(!_.isUndefined(source.publication_date.month))
        pubDate.push(source.publication_date.month);

      if(!_.isUndefined(source.publication_date.day))
        pubDate.push(source.publication_date.day);

      source.publication_date_string = pubDate.join('-');
      return source;
    }

    vm.source = parseSource(source);

    // fetch evidence items associated w/ source
    var query = {
      "operator": "AND",
      "queries": [
        {"field": "pubmed_id",
          "condition": {
            "name": "is",
            "parameters": [
              source.pubmed_id
            ]
          }
        }],
      "entity": "evidence_items",
      "save": true
    };

    Search.post(query)
      .then(function(response) {
        vm.evidence_items = response.results;
      });

    $scope.$on('suggestion:updated', function() {
      Sources.get(source.id).then(function(response) {
        angular.copy(response, vm.source);
      });
    });


  }
})();

(function() {
  'use strict';

  angular.module('civic.sources')
    .directive('sourceGrid', sourceGrid)
    .controller('SourceGridController', SourceGridController);

  // @ngInject
  function sourceGrid() {
    var directive = {
      restrict: 'E',
      replace: true,
      scope: {
        sources: '=',
        rows: '=',
        context:'='
      },
      templateUrl: 'app/views/sources/components/sourceGrid.tpl.html',
      controller: 'SourceGridController'
    };
    return directive;
  }

  // @ngInject
  function SourceGridController($scope,
                                $state,
                                uiGridConstants) {
    console.log('SourceGridController Loaded.');

    var vm = $scope.vm = {};

    vm.rowsToShow = $scope.rows ? $scope.rows : 10;
    vm.sourceGridOptions = {
      minRowsToShow: vm.rowsToShow - 1,
      //enablePaginationControls: true,
      //paginationPageSizes: [8],
      //paginationPageSize: 8,
      enablePaging: false,

      enableHorizontalScrollbar: uiGridConstants.scrollbars.NEVER,
      enableVerticalScrollbar: uiGridConstants.scrollbars.NEVER,
      enableFiltering: true,
      enableColumnMenus: false,
      enableSorting: true,
      enableRowSelection: true,
      enableRowHeaderSelection: false,
      multiSelect: false,
      modifierKeysToMultiSelect: false,
      noUnselect: true,
      columnDefs: [
        {
          name: 'pubmed_id',
          displayName: 'Pubmed ID',
          enableFiltering: true,
          allowCellFocus: false,
          type: 'string',
          width: '8%',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },

        {
          name: 'author_list_string',
          displayName: 'Authors',
          enableFiltering: true,
          allowCellFocus: false,
          type: 'string',
          width: '20%',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'publication_date_string',
          displayName: 'Date',
          type: 'string',
          enableFiltering: true,
          allowCellFocus: false,
          width: '10%'
        },
        {
          name: 'full_journal_title',
          displayName: 'Journal',
          type: 'string',
          enableFiltering: true,
          allowCellFocus: false,
          width: '15%'
        },
        {
          name: 'abstract',
          displayName: 'Abstract',
          type: 'string',
          allowCellFocus: false,
          enableFiltering: true,
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        }
      ]
    };

    vm.sourceGridOptions.onRegisterApi = function (gridApi) {
      var sources = $scope.sources;
      vm.gridApi = gridApi;

      vm.context = $scope.context;
      vm.sourceGridOptions.data = prepSources(sources);

      $scope.$watchCollection('sources', function (sources) {
        vm.sourceGridOptions.minRowsToShow = sources.length + 1;
        vm.sourceGridOptions.data = prepSources(sources);
      });

      gridApi.selection.on.rowSelectionChanged($scope, function (row) {
        var params = {sourceId: row.entity.id};
        if (event.metaKey) {
          // if meta key (alt or command) pressed, generate a state URL and open it in a new tab/window
          // shift would be preferable to meta but ui-grid's selection module appears to be capturing shift-clicks for multi-select feature
          // keep an eye on: https://github.com/angular-ui/ui-grid/issues/4926
          var url = $state.href('sources.summary', params, {absolute: true});
          $window.open(url, '_blank');
        } else {
          $state.go('sources.summary', params);
        }
      });

      function prepSources(sources) {
        return _.map(sources, function(source) {
          // TODO: refactor this into the service, we're doing the same munging in 3 places
          // format publication date
          var pubDate = [source.publication_date.year];
          if(!_.isUndefined(source.publication_date.month))
            pubDate.push(source.publication_date.month);

          if(!_.isUndefined(source.publication_date.day))
            pubDate.push(source.publication_date.day);

          source.publication_date_string = pubDate.join('-');

          // format author list
          source.author_list_string = _(source.author_list)
            .sortBy('position')
            .map(function(author) {
              return [author.fore_name, author.last_name].join(' ');
            })
            .value()
            .join(', ');
          return source;
        });
      }
    };
  }

})();

(function() {
  'use strict';

  angular.module('civic.sources')
    .directive('sourceSuggestionGrid', sourceSuggestionGrid)
    .controller('SourceSuggestionGridController', SourceSuggestionGridController);

  // @ngInject
  function sourceSuggestionGrid() {
    var directive = {
      restrict: 'E',
      replace: true,
      scope: {
        suggestions: '=',
        rows: '=',
        mode:'='
      },
      templateUrl: 'app/views/sources/components/sourceSuggestionGrid.tpl.html',
      controller: 'SourceSuggestionGridController'
    };
    return directive;
  }

  // @ngInject
  function SourceSuggestionGridController($scope,
                                          $state,
                                          Sources,
                                          uiGridConstants) {
    console.log('SourceSuggestionGridController Loaded.');

    var vm = $scope.vm = {};

    var mode = $scope.mode;

    vm.setSuggestion = function(id, status) {
      Sources.setStatus({suggestionId: id, status: status}).then(function(response) {
        $scope.$emit('suggestion:updated');
      });
    };

    var statusFilters = ['new'];

    vm.rowsToShow = $scope.rows ? $scope.rows : 10;
    vm.sourceSuggestionGridOptions = {
      minRowsToShow: vm.rowsToShow - 1,
      //enablePaginationControls: true,
      //paginationPageSizes: [8],
      //paginationPageSize: 8,
      enablePaging: false,

      enableHorizontalScrollbar: uiGridConstants.scrollbars.NEVER,
      enableVerticalScrollbar: uiGridConstants.scrollbars.NEVER,
      enableFiltering: true,
      enableColumnMenus: false,
      enableSorting: true,
      columnDefs: [
        {
          name: 'status',
          displayName: 'Status',
          enableFiltering: true,
          allowCellFocus: false,
          type: 'string',
          width: '6%',
          filter: {
            noTerm: true,
            condition: function(searchTerm, cellValue) {
              return _.contains(statusFilters, cellValue);
            }
          }
        },
        {
          name: 'created_at',
          displayName: 'Submitted',
          width: '8%',
          enableFiltering: false,
          allowCellFocus: false,
          visible: false,
          type: 'date',
          sort: {
            direction: uiGridConstants.DESC
          },
          cellTemplate: '<div class="ui-grid-cell-contents">{{ row.entity[col.field] | timeAgo }}</div>'
        },
        {
          name: 'submitter',
          displayName: 'Submitter',
          enableFiltering: true,
          allowCellFocus: false,
          type: 'string',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          },
          cellTemplate: '<div class="ui-grid-cell-contents"><user-block user="row.entity[col.field]"</div>',
        },
        {
          name: 'pubmed_id',
          displayName: 'Pubmed ID',
          width: '10%',
          visible: false,
          //visible: mode === 'full',
          enableFiltering: true,
          allowCellFocus: false,
          type: 'string',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'citation',
          displayName: 'Citation',
          visible: mode === 'full',
          enableFiltering: true,
          allowCellFocus: false,
          width: '15%',
          type: 'string',
          cellTemplate: 'app/views/sources/components/cellTemplateCitation.tpl.html',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'gene',
          displayName: 'Gene',
          width: '6%',
          type: 'string',
          allowCellFocus: false,
          enableFiltering: true,
          cellTemplate: 'app/views/events/common/geneGrid/tooltipCell.tpl.html',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'variant',
          displayName: 'Variant',
          width: '12%',
          type: 'string',
          allowCellFocus: false,
          enableFiltering: true,
          cellTemplate: 'app/views/events/common/geneGrid/tooltipCell.tpl.html',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'disease',
          displayName: 'Disease',
          type: 'string',
          allowCellFocus: false,
          enableFiltering: true,
          cellTemplate: 'app/views/events/common/geneGrid/tooltipCell.tpl.html',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'initial_comment',
          displayName: 'Comment',
          type: 'string',
          allowCellFocus: false,
          enableFiltering: true,
          cellTemplate: 'app/views/events/common/geneGrid/tooltipCell.tpl.html',
          filter: {
            condition: uiGridConstants.filter.CONTAINS
          }
        },
        {
          name: 'action',
          displayName: 'Actions',
          width: '15%',
          allowCellFocus: false,
          enableFiltering: false,
          cellTemplate: 'app/views/sources/components/cellTemplateActions.tpl.html'
        }
      ],
      // grid menu
      enableGridMenu: true,
      gridMenuShowHideColumns: false,
      gridMenuCustomItems: [
        {
          title: 'Show Curated',
          active: function() {
            return _.contains(statusFilters, 'curated');
          },
          action: function($event) {
            filterByStatus('curated', this.grid, $event);
          }
        },
        {
          title: 'Show Rejected',
          active: function() {
            return _.contains(statusFilters, 'rejected');
          },
          action: function($event) {
            filterByStatus('rejected', this.grid, $event);
          }
        }
      ]
    };

    function filterByStatus(status, grid) {
     if(_.contains(statusFilters, status)) {
        _.pull(statusFilters, status);
      } else {
        statusFilters.push(status);
      }
      grid.queueGridRefresh();
    }

    vm.sourceSuggestionGridOptions.onRegisterApi = function (gridApi) {
      var suggestions = $scope.suggestions;
      vm.gridApi = gridApi;

      vm.context = $scope.context;
      vm.sourceSuggestionGridOptions.data = prepSuggestions(suggestions);

      $scope.$watchCollection('suggestions', function (suggestions) {
        vm.sourceSuggestionGridOptions.minRowsToShow = suggestions.length + 1;
        vm.sourceSuggestionGridOptions.data = prepSuggestions(suggestions);
      });

      function prepSuggestions(suggestions) {
        return _.map(suggestions, function(source) {
          var urlBase = '#/add/evidence/basic';
          var urlElements = [];
          if(_.has(source, 'gene')) {
            urlElements.push('geneName=' + source.gene);
          }
          if(_.has(source, 'variant')) {
            urlElements.push('variantName=' + source.variant);
          }
          if(_.has(source, 'disease')) {
            urlElements.push('diseaseName=' + source.disease);
          }
          if(_.has(source, 'pubmed_id')) {
            urlElements.push('pubmedId=' + source.pubmed_id);
          }

          urlElements.push('sourceSuggestionId=' + source.id);

          source.addEvidenceUrl = urlBase + '?' + urlElements.join('&');
          return source;
        });
      }
    };
  }

})();

