module.run(['$templateCache', function($templateCache) {
  $templateCache.put('app/pages/help_evidence_levels.tpl.html',
    '<div class="help-sub-tab"><p>Each evidence statement is the result of an experiment, trial or study in published literature. It is important to capture the nature of these experiments in the evidence entry. Evidence levels allow for the subject of an evidence item to be presented in a simple, standardized fashion. The evidence level is also an indication of how close each assertion is to actual application in the clinic. Please, note that while evidence statements of all levels are acceptable in CIViC, by far the highest priority are levels A and B, followed by C, D, E. Our top priority is to document the evidence for application of variant interpretations to real patients in the clinic today. The more time and development needed to determine the relevance of a variant to real patients in the clinic, the lower the priority for curation. Reviewing and approving evidence items requires a serious time committment by the community. Please keep this in mind and try to direct your efforts to the most immediately clinically relevant evidence first.<table class="table table-striped table-condensed table-bordered"><colgroup><col width="150"><col width="300"><col width="920"></colgroup><tr><th>Level</th><th>Definition</th><th>Examples and further comments</th></tr><tr><td class="level levelA"><span class="character">A</span><br>Validated association</td><td>Proven/consensus association in human medicine.</td><td><i>"AML with mutated NPM1" is a provisional entity in WHO classification of acute myeloid leukemia (AML). This mutation should be tested for in clinical trials and is recommended for testing in patients with cytogenetically normal AML.</i> Validated associations are often in routine clinical practice already or are the subject of major clinical trial efforts.</td></tr><tr><td class="level levelB"><span class="character">B</span><br>Clinical evidence</td><td>Clinical trial or other primary patient data supports association.</td><td><i>BRAF V600E is correlated with poor prognosis in papillary thyroid cancer in a study of 187 patients with PTC and other thyroid diseases.</i> The evidence should be supported by observations in multiple patients. Additional support from functional data is desirable but not required.</td></tr><tr><td class="level levelC"><span class="character">C</span><br>Case study</td><td>Individual case reports from clinical journals.</td><td><i>A single patient with FLT3 over-expression responded to the FLT3 inhibitor sunitinib.</i> The study may have involved a large number of patients, but the statement was supported by only a single patient. In some cases, observations from just a handful of patients (e.g. 2-3) or a single family may also be considered a case study/report.</td></tr><tr><td class="level levelD"><span class="character">D</span><br>Preclinical evidence</td><td><i>In vivo</i> or <i>in vitro</i> models support association.</td><td><i>Experiments showed that AG1296 is effective in triggering apoptosis in cells with the FLT3 internal tandem duplication.</i> The study may have involved some patient data, but support for this statement was limited to in vivo or in vitro models (e.g. mouse studies, cell lines, molecular assays, etc.).</td></tr><tr><td class="level levelE"><span class="character">E</span><br>Inferential association</td><td>Indirect evidence.</td><td><i>CD33 and CD123 expression were significantly increased in patients with NPM1 mutation with FLT3-ITD, indicating these patients may respond to combined anti-CD33 and anti-CD123 therapy.</i> The assertion is at least one step removed from a direct association between a variant and clinical relevance.</td></tr></table></p></div>');
}]);
