  $templateCache.put('app/pages/help_report_problem.tpl.html',
    '<div><div class="row"><div class="col-xs-12"><h3 class="subtitle">Report a Problem</h3><p>If you discover a bug or something isn\'t working quite right, we want to know about it! Please <a href="https://github.com/genome/civic-client/issues">submit an issue to the civic-client github project</a>. We monitor these issues closely and will be able to collaborate on getting the problem resolved quickly.</p><p>If for some reason you do not wish to use github, you may <a href="mailto:civic-help@genome.wustl.edu?subject=CIViC Issue Report">send an email to the civic-help email list</a>, to which CIViC developers and researchers are all subscribed.</p><p>We\'d prefer to use Github for handling bugs, since the resolution to any issue will be public for anyone to see and benefit from.</p></div></div><div class="row"><div class="col-xs-6"><a class="btn btn-block btn-default" href="https://github.com/genome/civic-client/issues">Submit an Issue on github</a></div><div class="col-xs-6"><a class="btn btn-block btn-default" href="mailto:civic-help@genome.wustl.edu?subject=CIViC Issue Report">Send an Email to civic-help</a></div></div></div>');
